import React, { useState } from 'react';
import { Box, Stack, Typography, TextField, Button } from '@mui/material';

const BmiCalculator = () => {
  // Set state for weight, height, and age
  const [weight, setWeight] = useState();
  const [height, setHeight] = useState();
  const [age, setAge] = useState();
  const [BMI, setBMI] = useState("Not Calculated");
  const [health, setHealth] = useState("Not Calculated");
  const [healthyBmiRange] = useState("18.5 - 25"); // Healthy BMI range for adults

  const calculateBMI = () => {
    if (weight > 40 && weight < 160 && height > 130 && height < 230 && age > 0 && age < 80) {
      const heightInCentiMeters = height / 100; // Convert height to meters
      const calculatedBMI = (weight / (heightInCentiMeters ** 2)).toFixed(2); // BMI formula

      setBMI(calculatedBMI);

      // Determine health status based on BMI
      if (calculatedBMI < 18.5) {
        setHealth("Underweight");
      } else if (calculatedBMI >= 18.5 && calculatedBMI <= 25) {
        setHealth("Normal");
      } else if (calculatedBMI >= 25 && calculatedBMI <= 30) {
        setHealth("Overweight");
      } else {
        setHealth("Obese");
      }
    } else {
      alert("Please enter valid values.");
    }
  };

  return (
    <div>
      <Stack p="80px" gap="30px" backgroundColor="#FFF" width="90%" margin="auto" mt="100px">
        <TextField
          type="number"
          placeholder="Your Weight in Kg (must be 40kg to 160kg)"
          onChange={(e) => setWeight(e.target.value)}
        />
        <TextField
          type="number"
          placeholder="Your Height in CM (must be 130cm to 230cm)"
          onChange={(e) => setHeight(e.target.value)}
        />
        <TextField
          type="number"
          placeholder="Your Age (must be 0 to 80)"
          onChange={(e) => setAge(e.target.value)}
        />
        <Button variant="contained" color="primary" onClick={calculateBMI}>
          Calculate BMI
        </Button>
      </Stack>
      {BMI === "Not Calculated" ? null : (
        <Stack p="80px" gap="30px" backgroundColor="#FFF" width="90%" margin="auto" mt="100px">
          <Typography variant="h3">BMI Results</Typography>
          <Box>
            <Typography variant="h5">
              BMI: <span style={{ color: "#ffb700", fontWeight: "600", fontSize: "40px" }}>{BMI}</span>
            </Typography>
            <Typography variant="h5">
              Health: <span style={{ color: "#ffb700", fontWeight: "600", fontSize: "40px" }}>{health}</span>
            </Typography>
            <Typography variant="h5">
              Healthy BMI Range: <span style={{ color: "#ffb700", fontWeight: "600", fontSize: "40px" }}>
                {healthyBmiRange}
              </span>
            </Typography>
          </Box>
        </Stack>
      )}
    </div>
  );
};

export default BmiCalculator;
