import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { AppBar, Toolbar, IconButton, Drawer, List, ListItem, Stack } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import Logo from "../assets/images/Logo.png";

const Navbar = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);
const location=useLocation();
  const toggleDrawer = (open) => (event) => {
    if (event.type === "keydown" && (event.key === "Tab" || event.key === "Shift")) {
      return;
    }
    setDrawerOpen(open);
  };

  useEffect(()=>{
    if(location.hash==="#exercises"){
      const element=document.getElementById("exercises");
      if(element){
        element.scrollIntoView({behavior:"smooth"});
      }
    }
  }, [location]);
 
  return (
    <AppBar position="static" sx={{ background: "#FFFAFB", boxShadow: "none" }}>
      <Toolbar
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: { sm: "0 40px", xs: "0 20px" },
        }}
      >
        {/* Logo */}
        <Link to="/">
          <img
            src={Logo}
            alt="logo"
            style={{ width: "48px", height: "48px" }}
          />
        </Link>


        <Stack
          direction="row"
          spacing={4}
          sx={{
            display: { xs: "none", sm: "flex" }, // Hide links on small screens
            alignItems: "center",
            marginRight: "950px",
          }}
        >
          <Link
            to="/"
            style={{
              textDecoration: "none",
              color: "#3A1212",
              borderBottom: "3px solid #FF2625",
              fontSize: "18px",
            }}
          >
            Home
          </Link>

          <Link
            to="/about"
            style={{
              textDecoration: "none",
              color: "#3A1212",
              fontSize: "18px",
            }}
          >
            About
          </Link>
         
          <Link
            to="/#exercises"
            style={{
              textDecoration: "none",
              color: "#3A1212",
              fontSize: "18px",
            }}
          >
            Exercises
          </Link>

          <Link
            to="/BmiCalculator"
            style={{
              textDecoration: "none",
              color: "#3A1212",
              fontSize: "18px",
            }}
          >
            BMICalculator
          </Link>
  
        </Stack>

        <Stack
          direction="row"
          spacing={1}
          sx={{
            display: { xs: "flex", sm: "none" }, // Hide links on small screens
            marginLeft: "auto",
          }}
        >

          <ListItem buttons>
            <Link
              to="/"
              style={{
                textDecoration: "none",
                color: "#3A1212",
                borderBottom: "3px solid #FF2625",
                display: { xs: "block", sm: "none" },
                fontSize: "18px",
              }}
            >
              Home
            </Link>
          </ListItem>


          {/* Hamburger Menu Button */}
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ display: { sm: "none" } }} // Show only on small screens
            onClick={toggleDrawer(true)}
          >
            <MenuIcon style={{ color: "#3A1212" }} />
          </IconButton>
        </Stack>

        {/* Drawer for Mobile Navigation */}
        <Drawer
          anchor="right"
          open={drawerOpen}
          onClose={toggleDrawer(false)}
        >

          <List
            sx={{
              width: 250,
              padding: "20px",
            }}
            onClick={toggleDrawer(false)}
            onKeyDown={toggleDrawer(false)}
          >
<ListItem buttons>
              <Link
                to="/About"
                style={{
                  textDecoration: "none",
                  color: "#3A1212",
                  fontSize: "18px",
                }}
              >
                About
              </Link>
              </ListItem>
              
            <ListItem buttons>
              <Link
                to="/#exercises"
                style={{
                  textDecoration: "none",
                  color: "#3A1212",
                  fontSize: "18px",
                }}
              >
                Exercises
              </Link>
            </ListItem>
            <ListItem buttons>
              <Link
                to="/BmiCalculator"
                style={{
                  textDecoration: "none",
                  color: "#3A1212",
                  fontSize: "18px",
                }}
              >
                BMICalculator
              </Link>
            </ListItem>


          </List>
        </Drawer>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;