import React from 'react';
import { Box, Typography, List, ListItem, ListItemText, Paper } from '@mui/material';

const about = () => {
  return (
    <Box
      sx={{
        padding: '20px',
        maxWidth: '900px',
        margin: 'auto',
        textAlign: 'center',
        backgroundColor: '#f5f5f5',
        borderRadius: '8px',
      }}
    >
      <Typography variant="h3" gutterBottom>
        About the BodyBoost Application
      </Typography>
      <Typography variant="body1" paragraph>
        Welcome to the Exercise Search App, a powerful tool built with React and Material UI to help you find and explore exercises tailored to your fitness needs. With integrations like the Rapid API and YouTube API, this app brings a dynamic and interactive experience to your fitness journey, offering detailed exercise information, workout tutorials, and more.
      </Typography>
      <Typography variant="body1" paragraph>
        Whether you're a beginner or a seasoned athlete, this app helps you discover exercises targeting specific muscle groups, using different types of equipment, and provides seamless navigation to find the right workout routine for you.
      </Typography>

      <Typography variant="h4" gutterBottom>
        Key Features
      </Typography>
      <Paper sx={{ padding: '20px', backgroundColor: '#fafafa' }}>
        <List>
          <ListItem>
            <ListItemText primary="🔯 Search Exercises" secondary="Search for exercises based on body parts, equipment, or exercise names." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Exercise Categories" secondary="Explore exercises organized by body parts like arms, chest, legs, etc." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Pagination" secondary="Navigate large sets of exercises efficiently using pagination." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Detailed Exercise Information" secondary="Get detailed info such as targeted muscles, equipment, and demonstration videos." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 YouTube API Integration" secondary="Watch embedded YouTube videos for exercises and equipment demonstrations." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Similar Exercises" secondary="Discover exercises that target the same muscle groups or use similar equipment." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Responsive Design" secondary="Enjoy a consistent and responsive experience across all devices, from mobile to desktop." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Horizontal Scroll Menu" secondary="Easily navigate through body part categories using an intuitive horizontal scroll menu." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Loader" secondary="Visual feedback while fetching data from APIs, ensuring a smooth user experience." />
          </ListItem>
          <ListItem>
            <ListItemText primary="🔯 Error Handling" secondary="Displays error messages in case of API failures or unexpected issues." />
          </ListItem>
        </List>
      </Paper>

      <Typography variant="h4" gutterBottom sx={{ marginTop: '30px' }}>
        How to Use the App
      </Typography>
      <Typography variant="body1" paragraph>
        Getting started with the Exercise Search App is simple! Just follow these steps to make the most of the app:
      </Typography>
      <List>
        <ListItem>
          <ListItemText primary="1. Search for Exercises" secondary="Use the search bar to find exercises based on body parts, equipment, or specific keywords." />
        </ListItem>
        <ListItem>
          <ListItemText primary="2. Browse Exercise Categories" secondary="Use the horizontal scroll menu to explore exercises categorized by body parts." />
        </ListItem>
        <ListItem>
          <ListItemText primary="3. View Exercise Details" secondary="Click on any exercise to view detailed information, including muscle groups, equipment, and YouTube videos." />
        </ListItem>
        <ListItem>
          <ListItemText primary="4. Discover Similar Exercises" secondary="Explore exercises that target similar muscle groups or use similar equipment." />
        </ListItem>
        <ListItem>
          <ListItemText primary="5. Watch Exercise Tutorials" secondary="Access exercise demonstration videos embedded from YouTube for step-by-step guidance." />
        </ListItem>
        <ListItem>
          <ListItemText primary="6. Navigate with Pagination" secondary="Browse through exercises using pagination, making it easy to find the perfect workout." />
        </ListItem>
      </List>
    </Box>
  );
};

export default about;